import React from "react";
import Loading from "../../Components/Loading";
import PropTypes from 'prop-types'
import styled from 'styled-components'

const Container = styled.ul`
    max-width:50%;
    margin: 0 auto;
    padding-top: 50px;
    @media screen and (max-width:768px){
    padding-top: 10px;
    max-width:100%;
    }
`
const ListItem = styled.li`
    font-size:18px;
    color:white;
    padding:10px 0;
    justify-content:center;
    width:100%;
    margin:auto;
    @media screen and (max-width:768px){
        width:90%;
        margin:auto
    }
`

const Contents = styled.div`
    text-align:center;
    padding:20px;
    background:rgba(255,255,255,0.4);
    font-weight:500;
    color:white;
    &:first-child{
        color:black;
        padding:10px 0;
        font-weight:700;
        background-color: beige;
    }
    &:nth-child(2){
        color:greenyellow;
    }
    &:nth-child(3){
        border-top:2px solid red;
        padding: 10px 0;
        a{
        display:block;
        cursor: pointer;
        color: #baf8ba ;
        text-decoration:none;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    @media screen and (max-width:768px){
        font-size:15px;
        padding:0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        &:first-child{
        color:black;
        padding:10px 0;
        font-weight:700;
        background-color: beige;
    }
    &:nth-child(2){
        color:greenyellow;
    }
    &:nth-child(3){
        border-top:2px solid red;
        padding: 10px 0
        a{
        display:block;
        cursor: pointer;
        color:blue;
    }
    }
    }}
`
const ExchangesP = ({ loading, error, data }) => {
    return (
        <>
            {loading ? <Loading /> :
                <Container>{
                    data.map((i, index) =>
                        <ListItem key={index}>
                            <Contents>{i.name}</Contents>
                            <Contents>{i.description ? i.description : 'No description!'}</Contents>
                            <Contents>{i.links.website && i.links.website.map((i, index) => <a key={index} href={i}>{i}</a>)}</Contents>
                        </ListItem>)
                }</Container>
            }
        </>
    )
}

ExchangesP.propTypes = {
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    data: PropTypes.array
}
export default ExchangesP